import GseQrcodeScanner from '../../../components/GseQrcodeScanner'
import {REG_EXP_OFO_NUM} from '../../../utils/regExp'
export default {
  name: 'scanning-screen',
  components: { GseQrcodeScanner },
  data () {
    const scanW = Math.ceil(document.documentElement.clientWidth * 267 / 375)
    return {
      scanOptions: {
        rescanDelay: 30,
        innerLineW: 0,
        innerLineColor: 'transparent',
        borderW: 0,
        borderColor: 'transparent',
        outerLineW: 5,
        scanW,
        scanH: scanW
      }
    }
  },
  methods: {
    // 解码
    onDecode (code) {
      this.read(code)
    },
    //  获取车牌号
    read (code) {
      //  获取车牌号
      var arr = code.split('/')
      var carno = arr[arr.length - 1]
      if (!carno.match(REG_EXP_OFO_NUM)) { // 车牌号不满足ofo的规则
        this.$toast.text('车牌号不存在')
        setTimeout(() => {
          this.$refs.qrcodeReader.gseQrcode.startScan()
        }, 2000)
        return
      }
      // 开锁等待中
      this.$loading.show()
      // 请求单车密码
      $API.ofo.getUnlockCode({carno}).then((result) => {
        success(result)
      }).catch((e) => {
        this.$toast.text(e)
      }).finally(() => {
        this.$loading.hide()
        this.$refs.qrcodeReader.gseQrcode.startScan()
      })
      // 获取解锁码成功
      const success = (result) => {
        const {OK, code, msg, data} = result
        if (OK) {
          // 跳转解锁页
          const {pwd} = data
          this.$router.push({name: 'UnlockedCodeScreen', params: {pwd, carno}})
        } else {
          this.$toast.text(msg)
          switch (code) {
            case 30005: // 骑行中
              this.$router.push({name: 'RidingScreen'})
              break
            case 40078: // 骑行结束
              break
            case 40003: // 未结单
              break
            case 30009: // 车辆停靠在非法区域
              break
            case 30004: // 没有此车辆
              break
            case 50000: // 非法区域
              break
            case 50001: // 服务器响应
              break
            default: // 结单不成功
          }
        }
      }
    }
  },
  deactivated () {
    this.$refs.qrcodeReader.gseQrcode.stopScan()
  },
  activated () {
    this.$refs.qrcodeReader.gseQrcode.startScan()
  }
}



