/**
 * Created by fjl on 2018/10/19
 */
import qrcode from 'jsqrcode'
class GseQrcode {
  static NOT_SUPPORT_CAMERA_MSG = '对不起，您的浏览器不支持拍照功能，请使用其他浏览器'
  static NOT_SUPPORT_CANVAS_MSG = '浏览器不支持HTML5 CANVAS'
  // 手机屏幕的宽度
  static WIDTH = document.documentElement.clientWidth
  // 手机屏幕的高度
  static HEIGHT = document.documentElement.clientHeight
  // 扫码区域的宽度
  static SCAN_W = GseQrcode.WIDTH / 1.6
  // 扫码区域的宽度
  static SCAN_H = GseQrcode.SCAN_W
  // 扫码的矩形框的边角线的长度
  static INNER_LINE_L = 30
  // 扫码的矩形框的边角线高度
  static INNER_LINE_W = 4
  // 扫码区域的边框宽度
  static BORDER_W = 2
  // 扫码矩形框的颜色
  static BORDER_COLOR = '#FFE300'
  // 扫码矩形框的边角线颜色
  static INNER_LINE_COLOR = GseQrcode.BORDER_COLOR
  // 外框边角线的宽度
  static OUTER_LINE_W = 0
  // 外框边角线的宽度
  static OUTER_LINE_L = 30
  // 外框边角线的颜色
  static OUTER_LINE_COLOR = GseQrcode.BORDER_COLOR
  // 背景颜色
  static BACKGROUND_COLOR = 'rgba(0, 0, 0, 0.4)'
  // 解码频率(每秒解码多少次)
  static DECODE_FREQUENCY = 10
  // 从捕获到重启经历的时间，单位秒
  static RESCAN_DELAY = 1.5
  /**
   * @description 扫码组件的构造器
   * @param video: 播放摄像头（h5 video元素对象）
   * @param canvas: 捕捉摄像头图片（h5 canvas元素对象）
   * @param overlay: 绘制扫码的矩形框（h5 canvas元素对象）
   *  @param options: 扫码组件样式，动画等配置
   * @param decode: 捕获二维码的回调函数
   */
  constructor (video, canvas, overlay, scanLineContainer, options, decode) {
    const {
      borderColor,
      backgroundColor,
      scanW,
      scanH,
      borderW,
      innerLineL,
      innerLineW,
      innerLineColor,
      outerLineL,
      outerLineW,
      outerLineColor,
      decodeFrequency,
      rescanDelay
    } = options || {}
    this.video = video
    this.canvas = canvas
    this.overlay = overlay
    this.decode = decode || null
    this.scanLineContainer = scanLineContainer
    this.video.width = GseQrcode.WIDTH
    this.video.height = GseQrcode.HEIGHT
    this.overlay.width = GseQrcode.WIDTH
    this.overlay.height = GseQrcode.HEIGHT
    this.canvas.width = GseQrcode.SCAN_W
    this.canvas.height = GseQrcode.SCAN_H
    this.scanW = typeof scanW === 'number' ? scanW : GseQrcode.SCAN_W
    this.scanH = typeof scanH === 'number' ? scanH : GseQrcode.SCAN_H
    this.borderW = typeof borderW === 'number' ? borderW : GseQrcode.BORDER_W
    this.borderColor = borderColor || GseQrcode.BORDER_COLOR
    this.innerLineL = typeof innerLineL === 'number' ? innerLineL : GseQrcode.INNER_LINE_L
    this.innerLineW = typeof innerLineW === 'number' ? innerLineW : GseQrcode.INNER_LINE_W
    this.innerLineColor = innerLineColor || GseQrcode.INNER_LINE_COLOR
    this.outerLineL = typeof outerLineL === 'number' ? outerLineL : GseQrcode.OUTER_LINE_L
    this.outerLineW = typeof outerLineW === 'number' ? outerLineW : GseQrcode.OUTER_LINE_W
    this.outerLineColor = outerLineColor || GseQrcode.OUTER_LINE_COLOR
    this.backgroundColor = backgroundColor || GseQrcode.BACKGROUND_COLOR
    this.decodeFrequency = decodeFrequency || GseQrcode.DECODE_FREQUENCY
    this.rescanDelay = rescanDelay || GseQrcode.RESCAN_DELAY
    if (this.scanLineContainer) {
      this.scanLineContainer.style = `
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;margin: auto;
      width: ${this.scanW}px;
      height: ${this.scanH}px;
     `
    }
  }
  // 初次加载
  load () {
    return new Promise((resolve, reject) => {
      if (this.canvas.getContext) {
        this.playCamera().then(() => {
          // 绘制覆盖物
          this.drawOverlay()
          // 开启扫描功能
          this.startScan()
          resolve()
        }).catch(err => reject(err))
      } else {
        reject(new Error(GseQrcode.NOT_SUPPORT_CANVAS_MSG))
      }
    })
  }
  // 绘制覆盖物
  drawOverlay () {
    const drawOuterLine = () => {
      context.save()
      context.fillStyle = this.outerLineColor
      for (let i = 0; i < 4; i++) {
        context.save()
        context.translate(Math.floor(GseQrcode.WIDTH / 2), Math.floor(GseQrcode.HEIGHT / 2))
        context.rotate(Math.PI / 2 * i)
        const x = Math.floor(this.scanW / 2 - this.outerLineL + this.outerLineW)
        const y = Math.floor(this.scanH / 2 - this.outerLineL + this.outerLineW)
        context.fillRect(x, y, this.outerLineL, this.outerLineL)
        context.restore()
      }
      context.restore()
    }
    const drawInnerLine = () => {
      context.save()
      context.fillStyle = this.innerLineColor
      context.fillRect(
        Math.floor(GseQrcode.WIDTH / 2 - this.scanW / 2),
        Math.floor(GseQrcode.HEIGHT / 2 - this.scanW / 2),
        Math.floor(this.scanW),
        Math.floor(this.scanH)
      )
      const x2 = Math.floor(this.scanW / 2)
      const x1 = Math.floor(x2 - this.innerLineW)
      const x0 = Math.floor(x2 - this.innerLineL)
      const y2 = Math.floor(this.scanH / 2)
      const y1 = Math.floor(y2 - this.innerLineW)
      const y0 = Math.floor(y2 - this.innerLineL)
      context.translate(Math.floor(GseQrcode.WIDTH / 2), Math.floor(GseQrcode.HEIGHT / 2))
      context.beginPath()
      context.moveTo(x0, y2)
      context.lineTo(x0, y1)
      context.lineTo(x1, y1)
      context.lineTo(x1, y0)
      context.lineTo(x2, y0)
      context.lineTo(x2, -y0)
      context.lineTo(x1, -y0)
      context.lineTo(x1, -y1)
      context.lineTo(x0, -y1)
      context.lineTo(x0, -y2)
      context.lineTo(-x0, -y2)
      context.lineTo(-x0, -y1)
      context.lineTo(-x1, -y1)
      context.lineTo(-x1, -y0)
      context.lineTo(-x2, -y0)
      context.lineTo(-x2, y0)
      context.lineTo(-x1, y0)
      context.lineTo(-x1, y1)
      context.lineTo(-x0, y1)
      context.lineTo(-x0, y2)
      context.closePath()
      context.clip()
      context.clearRect(Math.floor(-this.scanW / 2), Math.floor(-this.scanH / 2), Math.floor(this.scanW), Math.floor(this.scanH))
      context.restore()
    }
    const context = this.overlay.getContext('2d')
    context.save()
    context.fillStyle = this.backgroundColor
    context.fillRect(0, 0, GseQrcode.WIDTH, GseQrcode.HEIGHT)
    context.strokeStyle = this.borderColor
    context.lineWidth = 2 * this.borderW
    context.strokeRect(GseQrcode.WIDTH / 2 - this.scanW / 2, GseQrcode.HEIGHT / 2 - this.scanW / 2, this.scanW, this.scanH)
    context.restore()
    drawOuterLine()
    drawInnerLine()
  }
  // 开始扫描
  startScan () {
    if (this.timer) {
      return
    }
    if (this.startScanCallback) this.startScanCallback()
    this.video.play()
    const {videoWidth, videoHeight} = this.video
    const context = this.canvas.getContext('2d')
    const cropW = videoWidth * this.scanW / GseQrcode.WIDTH
    const cropH = videoWidth * this.scanH / GseQrcode.WIDTH
    this.timer = setInterval(() => {
      context.drawImage(this.video,
        (videoWidth - cropW) / 2, (videoHeight - cropH) / 2, cropW, cropH,
        0, 0, this.scanW, this.scanH)
      try {
        qrcode.callback = (data) => {
          if (!(data instanceof Error)) {
            this.stopScan()
            setTimeout(() => {
              this.startScan()
            }, 1000 * this.rescanDelay)
            if (this.decode) {
              this.decode(data)
            }
          }
        }
        qrcode.decode(this.canvas.toDataURL('image/png'))
      } catch (e) {
      }
    }, 1000 / this.decodeFrequency)
  }
  stopScan () {
    if (this.timer) {
      clearInterval(this.timer)
      this.timer = null
    }
    this.video.pause()
  }
  // video 播放摄像头内容
  playCamera () {
    return new Promise((resolve, reject) => {
      // 获取相机视频流
      this.getCameraStream().then((stream) => {
        this.video.src = URL.createObjectURL(stream)
        this.video.onerror = (err) => {
          reject(err)
          stream.stop()
        }
        stream.onended = (err) => {
          reject(err)
        }
        this.video.onloadedmetadata = (data) => {
          resolve(data)
        }
      }).catch((err) => {
        reject(err)
      })
    })
  }
  // 获取相机视频流
  getCameraStream () {
    // 获取媒体对象
    navigator.getUserMedia = navigator.getUserMedia ||
      navigator.webkitGetUserMedia ||
      navigator.mozGetUserMedia ||
      navigator.msGetUserMedia
    return new Promise((resolve, reject) => {
      if (navigator.getUserMedia) {
        // 参数一获取用户打开权限，参数二成功打开后调用，参数三打开失败后调用
        navigator.getUserMedia({video: true, audio: false},
          (stream) => {
            resolve(stream)
          },
          (err) => {
            reject(err)
          })
      } else {
        reject(new Error(GseQrcode.NOT_SUPPORT_CAMERA_MSG))
      }
    })
  }
}

export default GseQrcode



